function getRoot() {
	return new Promise(function (resolve) {
		var next = function next() {
			if (!document.body) {
				setTimeout(next, 100);
				return;
			}

			resolve(document.body);
		};
		next();
	});
}

function removeNode(node) {
	return node.parentNode.removeChild(node);
}

export { getRoot, removeNode };
